/*-->> header style <<--*/
.App-header {
  /*background-color: rgb(127, 255, 212);
  padding: 3px 50px;
  color: white;
  position: relative;
  z-index: 10;*/

  background-color: rgb(255, 127, 151);
  padding: 3px 50px;
  color: white;
  position: fixed;
  z-index: 10;
  width: 100%;
}
.sk-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
/*-->> nav style <<--*/
.sk-header nav {
  width: 60%;
  position: absolute;
  right: 20px;
  z-index: 2;
  top: 4px;
}
nav ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  margin: 0;
}
@media only screen and (max-width: 1023px) {
  nav ul {
    display: none;
  }
}
  nav ul li {
    list-style: none;
    position: relative;
    -webkit-transform: rotate(20deg);
        -ms-transform: rotate(20deg);
            transform: rotate(20deg);
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    padding-top: 40px;
  }
  
  nav ul li:nth-child(2) {
    -webkit-transform: rotate(-20deg);
        -ms-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  nav ul li:nth-child(4) {
    -webkit-transform: rotate(-20deg);
        -ms-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  nav ul li.active,
  nav ul li:hover {
    -webkit-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
            transform: rotate(0deg);
    /*animation: moveIt 2.5s ease-in-out infinite;*/
  }
    nav ul li a {
      font-size: 18px;
      color: #fff;
      font-weight: 500;
      text-decoration: none;
      -webkit-transition: all 300ms ease-in-out;
      -o-transition: all 300ms ease-in-out;
      transition: all 300ms ease-in-out;
      border-radius: 5px;
      background-color: rgb(255, 127, 151);
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
      padding: 5px 10px;
      position: relative;
      z-index: 9;
      line-height: 1.4;

    }
    nav ul li a:hover {
      color: rgb(12, 25, 22);
    }
    nav ul li:not(:first-child):before {
      content: '';
      position: absolute;
      width: 130%;
      height: 2px;
      right: 75%;
      top: 16px;
      z-index: 0;
      background-color: rgb(255, 127, 151);
      z-index: 0;
      display: none;
    }
      nav ul li a span {
        height: 40px;
        width: 2px;
        background-color: rgb(255, 127, 151);
        position: absolute;
        left: calc(50% - 2px);
        top: -40px;
      }
      nav ul li a span:after {
        content: '';
        height: 45px;
        width: 2px;
        background-color: rgb(255, 127, 151);
        position: absolute;
        right: 8px;
        top: 0;
        -webkit-transform: rotate(25deg);
            -ms-transform: rotate(25deg);
                transform: rotate(25deg);
      }
      nav ul li a span:before {
        content: '';
        height: 45px;
        width: 2px;
        background-color: rgb(255, 127, 151);
        position: absolute;
        left: 8px;
        top: 0;
        -webkit-transform: rotate(-25deg);
            -ms-transform: rotate(-25deg);
                transform: rotate(-25deg);
      }
        
/*-->> logo style <<--*/
.sk-header .logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  font-size: 24px;
  font-weight: bold;
  color: lightcoral;
  position: absolute;
  left: 40px;
  top: 15px;
}
.logo img {
  width: 115px;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@-webkit-keyframes nav-li-spin {
  /*0% { transform: rotate(20deg); }
  50% { transform: rotate(0deg); }
  100% { transform: rotate(-20deg); }*/
}
@keyframes nav-li-spin {
  /*0% { transform: rotate(20deg); }
  50% { transform: rotate(0deg); }
  100% { transform: rotate(-20deg); }*/
}
@-webkit-keyframes moveIt {
  0%, 100% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }
  50% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
}
@keyframes moveIt {
  0%, 100% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
  }
  50% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
}

/* humburger menu */

#nav-icon1 {
  width: 30px;
  height: 24px;
  position: relative;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 6px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background: rgb(255, 127, 151);
  display: none;
  border-radius: 3px;
}
@media only screen and (max-width: 1023px) {
  #nav-icon1 {
    display: block;
  }
}
#nav-icon1 span {
  display: block;
  position: absolute;
  height: 4px;
  width: 30px;
  background: #fff;
  border-radius: 6px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg) translate(6px, 6px);
      -ms-transform: rotate(0deg) translate(6px, 6px);
          transform: rotate(0deg) translate(6px, 6px);
  -webkit-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0px;
}

#nav-icon1 span:nth-child(2) {
  top: 10px;
}

#nav-icon1 span:nth-child(3) {
  top: 20px;
}

#nav-icon1.open span:nth-child(1) {
  top: 12px;
  -webkit-transform: rotate(135deg) translate(-2px, -7px);
      -ms-transform: rotate(135deg) translate(-2px, -7px);
          transform: rotate(135deg) translate(-2px, -7px);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -30px;
}

#nav-icon1.open span:nth-child(3) {
  top: 12px;
  -webkit-transform: rotate(-135deg) translate(-7px, 2px);
      -ms-transform: rotate(-135deg) translate(-7px, 2px);
          transform: rotate(-135deg) translate(-7px, 2px);
}




