/*-->> Achievements style <<--*/
.sk-achievements-content {
  background-color: #FFC107;
  padding: 80px 60px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
  .sk-achievements-link {
    width: 40%;
    padding-right: 50px;
  }
    .sk-achievements-link h4 {
      font-size: 30px;
      color: #fff;
      font-weight: 400;
      text-align: left;
      position: relative;
      margin: 50px 0 30px;
    }
    .sk-achievements-link .link-to {
      font-size: 20px;
      line-height: 1.3;
      color: #fff;
      font-weight: 300;
      margin-bottom: 10px;
      display: inline-block;
      background-color: #00bcd4;
    }
    .sk-achievements-link .link-to:hover {
      background-color: #fff;
      color: #00bcd4;
    }
  .sk-achievements {
    margin: 0 auto;
    width: 60%;
  }
    .sk-achievements-list .slick-slide img {
        width: 100%;
    }


